import * as React from "react";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";

export default function MenuTop() {
  return (
    <Container
      disableGutters
      maxWidth="sm"
      component="main"
      sx={{ pt: 1, pb: 2 }}
    >
      <Typography
        component="h1"
        variant="h2"
        align="center"
        color="white"
        /*color="text.primary"*/
        gutterBottom
      >
        My Champion Pool
      </Typography>
      <Typography
        variant="h5"
        align="center"
        /*color="text.secondary"*/ color="silver"
        component="p"
      >
        List the top 8 champions who can play as I see fit, lane by lane.
        Quickly check item builds with links to statistics websites.
      </Typography>
    </Container>
  );
}
